.hero__content {
  max-width: 577px;
  color: var(--color-light);
}

.hero-slider {
  position: relative;
  width: 100%;
  overflow: hidden;

  &__nav {
    position: absolute;
    width: 100%;
    justify-content: space-between;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    z-index: 15;
    padding: 0 22px;
  }

  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    // bottom: 120px;
    // left: 332px;
  }

  &__pag {
    position: absolute;
    display: flex;
    align-items: center;

    bottom: 120px !important;
    left: calc((100% - 1230px) / 2) !important;

    counter-reset: pag;
    .swiper-pagination-bullet {
      display: flex;
      flex-direction: column;
      border-radius: 0;
      width: 178px;
      height: auto;
      text-align: left;
      background-color: transparent;
      margin: 0 2.3px !important;
      opacity: 1;

      &::before {
        counter-increment: pag;
        content: "0" counter(pag);

        font-weight: 700;
        font-size: 28px;
        line-height: 150%;
        color: rgba($color: #fff, $alpha: 0.6);
        margin-bottom: 10px;
        transition: color var(--hero-slider-speed) ease-in-out;
      }

      &::after {
        display: none;
      }
    }

    .swiper-pagination-bullet-active {
      &::before {
        color: rgba($color: #fff, $alpha: 1);
      }

      .hero-slider__bar::after {
        transform: scaleX(1);
        transition: transform var(--hero-slider-speed) ease-in-out;
      }
    }
  }

  &__bar {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 2px;
    border-radius: 100px;
    background-color: rgba($color: #fff, $alpha: 0.3);

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-light);
      border-radius: 100px;
      transform-origin: left;
      transform: scaleX(0);
    }
  }
}

.hero-slide {
  padding-top: 221px;
  padding-bottom: 352px;
  background-color: var(--color-gray);

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  background-image: url(../img/hero-bg.jpg);

  max-height: 988px;
}
