.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;

  height: 500px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;

    z-index: 3;

    background-color: rgba($color: #1e212c, $alpha: 0.6);
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    @include flex-all-center;

    position: absolute;
    top: 50%;
    left: 50%;

    border-radius: 100%;
    width: 80px;
    height: 80px;

    background-color: var(--color-primary);

    transform: translate(-50%, -50%);
    z-index: 4;
    outline-color: rgba(#ff5a30, 0);

    transition: all 0.3s ease-in-out;

    svg {
      fill: var(--color-light);
      width: 26px;
      height: 26px;

      margin-right: -4px;
    }

    &:focus {
      outline-offset: 5px;
      outline: 2px solid var(--color-primary);
    }

    &:hover {
      outline: 16px solid;
      outline-color: rgba(#ff5a30, 0.12);
      outline-offset: 0px;
      background-color: var(--color-active);
    }

    &:active {
      outline: 16px solid;
      outline-color: rgba(#fc3300, 0.12);
      outline-offset: 0px;
      background-color: var(--color-active);
    }
  }

  &__muted {
    @include flex-all-center;
    position: absolute;
    bottom: 32px;
    left: 32px;

    border-radius: 100%;
    width: 40px;
    height: 40px;

    background-color: rgba($color: #fff, $alpha: 0.12);
    z-index: 4;
    outline-color: rgba(#787a80, 0);
    transition: all 0.3s ease-in-out;

    svg {
      fill: var(--color-light);
      width: 20px;
      height: 20px;
    }

    &:focus {
      outline-offset: 5px;
      outline: 2px solid var(--color-storm);
    }

    &:hover {
      outline: 16px solid;
      outline-color: rgba(#787a80, 0.52);
      outline-offset: 0px;
    }

    &:active {
      outline: 16px solid;
      outline-color: rgba(#787a80, 0.72);
      outline-offset: 0px;
    }
  }
}
